.product-slide-info {
	position: fixed;
	// top: var(--header-height, 146px);
	top: 0;
	bottom: 0;
	// padding-bottom: var(--footer-height, 72px);
	right: 0;
	background: white;
	z-index: 1000;
	width: 426px;
	overflow: visible;
	transform: translateX(100%);
	transition: all ease .3s;

	&.show {
		transform: translateX(0);
		box-shadow: -8px 0 8px 0 rgba(34, 60, 80, 0.2);
	}

	.product-slide-content {
		border-left: 2px solid $light-grey3;
		padding: 32px 24px 32px 32px;
		display: flex;
		flex-direction: column;
		align-items: stretch;
	}

	.header {
		display: flex;
		align-items: center;
		flex-shrink: 0;

		.icon {
			margin-left: auto;
			cursor: pointer;
		}
	}

	.body {
		.slider {
			margin-bottom: 40px;

			.slide {
				position: relative;
				width: 100%;
				padding-bottom: 100%;

				.content {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;

					img {
						width: 100%;
						height: 100%;
						object-fit: contain;
						object-position: center;
					}
				}
			}

			.splide {
				&__pagination {
					margin-top: 16px;

					li {
						display: flex;

						+ li {
							margin-left: 8px;
						}
					}

					&__page {
						width: 8px;
						height: 8px;
						border-radius: 50%;
						background: $light-grey2;
						border: none;
						cursor: pointer;

						&.is-active {
							background: $red;
						}
					}
				}
			}
		}

		.subslider {
			display: flex;
			align-items: center;

			.favourite {
				@extend %icon,
				%icon-heart;
				cursor: pointer;
				margin-left: auto;

				&.active {
					@extend %icon,
					%icon-heart-filled;
					background: $red;
				}
			}

			.labels {
				width: 100%;
				margin-right: 16px;
			}
		}

		.subtitle {
			margin-top: 8px;
			color: $grey2;
		}

		.characteristics {
			margin-top: 24px;

			.title {
				color: $grey1;
				margin-bottom: 16px;
			}

			.text-items {
				.item {
					+ .item {
						margin-top: 8px;
					}

					.name {
						color: $grey2;
						display: inline;
					}

					.value {
						color: $black;
						@extend %text;
						display: inline;
					}
				}
			}

			.progress-items {
				margin-top: 24px;

				.item {
					+ .item {
						margin-top: 16px;
					}

					.progress {
						height: 8px;
						background: $light-grey3;
						position: relative;
						border-radius: 2px;
						margin-bottom: 4px;

						&::before {
							max-width: 100%;
							content: "";
							height: 100%;
							position: absolute;
							top: 0;
							left: 0;
							width: var(--val);
							background: $red;
							border-radius: 2px 0 0 2px;
						}
					}
				}
			}
		}

		.description {
			margin-top: 24px;
			@extend %text-content;
		}
	}

	@media (max-width: $tablet) {
		width: 100%;
		padding-bottom: var(--footer-height, 72px);
	}
}