* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	outline: none;
	font-family: Inter;
	text-decoration: none;

	&::before,
	&::after {
		box-sizing: border-box;
		margin: 0;
		padding: 0;
		outline: none;
		font-family: Inter;
	}
}

:root {
	--header-margin: 32px;
}

img {
	max-width: 100%;
	object-fit: contain;
	display: block;
}

html {
	scroll-behavior: smooth;

	&.noscroll {
		overflow: hidden;
	}
}

body {
	min-height: calc(var(--vh, 1vh) * 100);
	width: 100%;
	// height: 100%;
	display: flex;
	flex-direction: column;
	justify-content: space-between;

	main {
		height: 100%;
		flex-grow: 1;
		display: flex;
		flex-direction: column;

		> * {
			width: 100%;
		}
	}

	&.noscroll {
		overflow: hidden;
		top: calc(-1 * var(--scroll-position, 0));
		padding-right: var(--scrollbar-width, 0);
		position: fixed;
		width: 100%;
	}
}

@mixin transition-base {
	transition: all .2s linear;

	cursor: pointer;
}

.icon-link {
	display: flex;
	align-items: center;
	cursor: pointer;

	span {
		color: var(--link-color);

		+ .icon {
			margin-left: 8px;
		}
	}

	.icon {
		background: var(--link-color);

		+ span {
			margin-left: 8px;
		}
	}
}

.simple-sticky {
	position: sticky;
	top: calc(var(--header-height) + var(--header-margin));
}

.hidden {
	display: none !important;
}

main {
	// margin-bottom: 500px;
}

.information-icon {
	width: 16px;
	height: 16px;
	
	display: inline-block;

	cursor: pointer;

	background: url(../static/img/icon/icon-grey.svg) no-repeat;
	background-size: contain;
}

.product-header {
	.right {
		text-align: left !important;
		align-items: center !important;
		a {
			display: inline !important;
		}
	}
} 

