.out-of-stock {
	position: relative;

	cursor: pointer;

	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;

		width: 26px;
		height: 26px;
		display: block;

		background: url(../static/img/icon/box-light.svg) no-repeat;
		background-size: contain;

		transition: background .2s ease;
	}

	&:hover {
		&::before {
			background: url(../static/img/icon/box-dark.svg) no-repeat;
		}

		@media (min-width: $tablet) {
			.out-of-stock-content {
				opacity: 1;
			}
		}
	}

	@media (max-width: $tablet) {
		position: static;
	}
}

.in-stock {
	position: relative;

	cursor: pointer;

	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;

		width: 26px;
		height: 26px;
		display: block;

		background: url(../static/img/icon/check-green.svg) no-repeat;
		background-size: contain;

		transition: background .2s ease;
	}

	@media (max-width: $tablet) {
		position: static;
	}
}

.out-of-stock-content {
	position: absolute;
	z-index: 5;

	top: 37px;
	left: -15px;
	
	padding: 10px;

	font-size: 12px;
	line-height: 18px;
	font-weight: 500;
	white-space: nowrap;
	color: #1D1F20;
	text-align: left;

	opacity: 0;
	transition: opacity .2s ease;

	background: #FFFFFF;
	border-radius: 4px;
	filter: drop-shadow(1px 2px 6px rgba(0, 0, 0, 0.15));

	pointer-events: none;

	span {
		color: #d33836 !important
	}

	&::before {
		content: "";
		position: absolute;
		top: -11px;
		left: 15px;

		width: 0;
		height: 0;

		border-left: 11px solid transparent;
		border-right: 11px solid transparent;
		border-bottom: 11px solid #FFFFFF;
	}

	@media (max-width: $tablet) {
		display: none;
	}
}

.product-table-row .left .product-table-cell .right {
	> span {
		font-size: 12px !important;
		font-weight: 400 !important;
		line-height: 150% !important;
		display: block;

		margin: 12px 0;
	}

	.detail-opener {
		font-size: 13px !important;
		font-weight: 700 !important;
		line-height: 150% !important;
		display: inline;
	}
}

.product-table-row .left:has(.img-holder) {
	position: relative;

	.product-table-cell .product-header {

		.left {
			position: absolute;
			left: 28px;
			top: 16px;

			.favourite {
				margin: 0;
			}

			@media (max-width: $tablet) {
				left: 4px;
			}
		}
	}

	@media (max-width: $tablet) {
		display: grid;
		grid-template-columns: 120px 1fr 24px !important;

		.product-table-cell:nth-child(3) {
			grid-column: 3 / 4 !important;
			grid-row: 1 !important;
		}
	}
}

.product-table-row .left:has(:not(.img-holder)) {
	.product-table-cell:nth-child(1) {
		padding-left: 24px;

		@media (max-width: $tablet) {
			padding-left: 0;
		}
	}

	.product-header .left {
		margin-left: 4px;
	}

	
}

.product-table-row .left .img-holder {
	width: 120px;
	height: 120px;
	display: inline-block;
	aspect-ratio: 1 / 1;

	margin-top: 12px;
	margin-left: 24px;

	img {
		width: 100%;
		height: 100%;
		display: block;
	}

	@media (max-width: $tablet) {
		margin-left: 0;
	}
}

.product-table-row.disabled {
	.left .img-holder {
		opacity: .5;
	}
}
.catalog-nav {
	transition: width 0.2s linear !important;
}
.cart {
	transition: opacity 0.2s linear, transform 0.2s linear !important;
}
.product-table-row .left:has(.img-holder) {
	display: grid;
	grid-template-columns: 144px 1fr;

	.img-holder {
		grid-column: 1 / 2;
	}

	.product-table-cell:nth-child(2) {
		display: inline !important;
	}

	.product-table-cell:nth-child(3) {
		grid-column: 1 / 3;
		grid-row: 2;

		text-align: left;
	}
}

.checkout-warn {
	position: fixed;
	top: 50%;
	left: 50%;
	z-index: 1000;

	padding: 16px 24px;
	border-radius: 8px;
	background-color: #fff;

	display: flex;
	flex-direction: column;
	gap: 16px;

	transform: translate(-50%, -50%);

	button {
		min-width: 280px;
	}

	&.disable {
		display: none;
	}
}

.documents-block {
	margin-top: 32px;

	h3 {
		margin-bottom: 24px;
	}

	ul {
		list-style-type: none;

		display: flex;
		flex-direction: column;
		gap: 24px;

		.documents-item {
			position: relative;

			padding: 24px;
			border: 2px solid #ECEBE5;

			display: flex;
			gap: 24px;

			@media (max-width: $tablet) {
				gap: 8px;
			}

			.documents-item-icon {
				min-width: 80px;
				min-height: 80px;

				background: url(../static/img/icon/ion_document.svg) no-repeat;
				background-size: contain;

				@media (max-width: $tablet) {
					min-width: 40px;
					min-height: 40px;
				}
			}

			.subtitle {
				max-width: 616px;

				@media (max-width: $tablet) {
					padding-bottom: 40px;
				}
			}

			.documents-item-picker {
				position: absolute;
				top: 24px;
				right: 24px;

				border-radius: 6px;
				cursor: pointer;

				@media (max-width: $tablet) {
					top: auto;
					bottom: 24px;
				}

				&:before {
					content: "";
					display: block;
					width: 32px;
					height: 32px;
	
					background: url(../static/img/icon/doc-picker.svg) no-repeat;
					background-size: contain;

					transition: background .1s ease;
				}

				&:hover {
					background-color: #d33836;

					&:before {
						background: url(../static/img/icon/doc-picker-1.svg) no-repeat;
						background-size: contain;
					}
				}
			}
		}
	}
}

.goverlay {
	background-color: #fff !important;
}

.gbtn.disabled {
	display: none;
}

.gslide-media {
	box-shadow: none !important;
}

.gclose {
	position: absolute;
	top: 0 !important;
	right: 0 !important;

	width: 100px !important;
	height: 100px !important;

	padding: 28px;

	opacity: 1 !important;
	background-color: #ffffffc4 !important;

	svg {
		width: 26px !important;
		height: 26px !important;

		path {
			fill: #999 !important;
		}
	}

	&:hover svg path {
		fill: #d33836 !important;
	}

	@media (max-width: $tablet) {
		width: 80px !important;
		height: 80px !important;

		padding: 18px;
	}
}

.user-choose {
	display: flex;
	flex-direction: column;
	gap: 4px;
}

.user-choose .choices {
	border-radius: 4px;
	border: 2px solid rgba(242, 242, 242, 1);

	margin-bottom: 0;

	

	>.choices__inner {
		padding: 17.5px 32px 17.5px 24px !important;

		>.choices__list>.choices__item {
			color: rgba(198, 198, 198, 1);

			transition: color .2s ease;
		}
	}

	&.is-open>.choices__inner>.choices__list>.choices__item {
		color: #1D1F20;
	}

	&:after {
		right: 24px !important;
	}
}

